body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.header {
  padding: 2rem;
  background: #673ab7;
  color: #FFF;
  display: flex;
  justify-content: space-between; }
  .header .fa {
    color: #FFF; }
  .header-app-name {
    font-size: 2rem;
    flex: 0 0 10rem;
    cursor: pointer; }
  .header-login {
    font-size: 2rem;
    height: 2rem;
    display: flex;
    align-items: center; }
    .header-login .fa-sign-out {
      cursor: pointer;
      color: #b388ff; }
    .header-login-text {
      margin: 0 1rem;
      max-width: 20rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden; }

.login-page {
  max-width: 60rem; }
  .login-page .page-title {
    padding-bottom: 2rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12); }
  .login-page .login-form {
    margin-top: 5rem; }
  .login-page .btn-container {
    margin: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.2rem; }
  .login-page .admin-container {
    display: flex;
    align-items: center; }
  .login-page .password-input {
    margin: 0 1rem;
    width: 10rem;
    border: 1px solid black; }

.topic-list-page .page-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12); }
  .topic-list-page .page-title h1 {
    margin: 0; }

.topic-list-page .topic-item {
  margin: 2rem 0;
  display: flex; }
  .topic-list-page .topic-item a {
    margin-left: 2rem; }
  .topic-list-page .topic-item .topic-item-name {
    flex: 1 1;
    margin: 0; }
  .topic-list-page .topic-item .fa-times {
    margin-top: -0.2rem; }

.edit-topic-page .page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  margin-bottom: 2rem; }
  .edit-topic-page .page-header h1 {
    margin: 0; }

.edit-topic-page .btn-container {
  display: flex; }

.edit-topic-page .save-btn {
  margin-left: auto; }

.edit-topic-page .field {
  margin-bottom: 4rem; }

.edit-topic-page .field-label {
  margin-bottom: 1rem; }

.edit-topic-page label {
  margin-right: 2rem; }

.vote-page h1 {
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  margin-bottom: 2rem; }

.vote-page .candidate-item {
  display: flex;
  margin: 2rem 0;
  align-items: center; }
  .vote-page .candidate-item .votes-count {
    width: 6rem;
    display: flex;
    align-items: center; }
    .vote-page .candidate-item .votes-count .count {
      background-color: rgba(0, 0, 0, 0.308);
      padding: .4rem 1rem;
      border-radius: .3rem;
      color: #fff; }
    .vote-page .candidate-item .votes-count .place-1 {
      background-color: #7560f6 !important; }
    .vote-page .candidate-item .votes-count .place-2 {
      background-color: #08c3e4 !important; }
    .vote-page .candidate-item .votes-count .place-3 {
      background-color: #91d962 !important; }
  .vote-page .candidate-item .candidate-name {
    flex: 1 1; }
  .vote-page .candidate-item .vote-btn {
    margin-left: 2rem;
    cursor: pointer; }

.vote-page .is-voted {
  font-weight: bold; }

.new-candidate {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  margin-top: 2rem;
  padding-top: 2rem; }
  .new-candidate .save-btn {
    margin-top: 1rem; }

html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  background: #ede7f6;
  min-width: 30rem;
  font-size: 2rem; }

* {
  box-sizing: border-box; }

.hidden {
  display: none !important; }

.app-page {
  margin: 2rem auto;
  width: 100%;
  max-width: 80rem;
  background: white;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 4px rgba(0, 0, 0, 0.24);
  padding: 4rem; }

a {
  color: #0000ee;
  -webkit-text-decoration: underline solid #0000ee;
          text-decoration: underline solid #0000ee; }
  a:visited {
    color: #551a8b;
    -webkit-text-decoration: underline solid #551a8b;
            text-decoration: underline solid #551a8b; }

input[type='text'] {
  border: none;
  display: block;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  font-size: 2rem;
  padding: 0.3rem 0; }
  input[type='text']:focus {
    border: none;
    outline-color: transparent;
    border-bottom: 2px solid #4285f4; }

textarea {
  display: block;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
  padding: 0.3rem; }
  textarea:focus {
    border: 2px solid #4285f4; }

h1 {
  font-size: 3.4rem;
  line-height: 135%;
  margin: 0 0 1rem 0;
  font-weight: 200; }

p {
  margin: 0;
  font-size: 1.6rem; }

button {
  color: white;
  background: #4285f4;
  line-height: 2rem;
  min-width: 8.8rem;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  border: 0;
  border-radius: 3px;
  display: inline-block;
  font-size: 1.4rem;
  font-weight: 500;
  outline: none;
  overflow: hidden;
  text-align: center;
  text-transform: uppercase;
  -webkit-tap-highlight-color: transparent;
  padding: .8rem 1.6rem; }
  button:active {
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }
  button:disabled {
    background-color: grey; }

.fa {
  font-size: 2.5rem;
  height: 2.5rem;
  width: 2.5rem;
  color: #4285f4; }

.ant-switch {
  height: 3.6rem;
  line-height: 3rem; }
  .ant-switch .ant-switch-inner {
    padding: 0 1rem;
    font-size: 2rem; }
  .ant-switch::after {
    top: 0.8rem; }

